.sale {
    padding-bottom: 48px;
 }

 .sale .container {
    position: relative;
 }

@media screen and (min-width: 220px) {
    .sale {
    background: url(../images/sale/sale-back_mobile.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 100%;
    margin: 0 auto;
   
 }
}

@media screen and (min-width: 768px) {
    .sale {
        background: url(../images/sale/sale-back-tablet.jpg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        width: 768px;
        margin: 0 auto;
    }
}

@media screen and (min-width: 1280px) {
    .sale {
        background: url(../images/sale/sale-back-desktop.jpg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        width: 1280px;
        margin: 0 auto;
    }
}

.sale__title {
   
    font-weight: 500;
    font-size: 24px;
    line-height: 1.16;
    margin-bottom: 35px;
    color: var(--primary--color);
    margin-left: 65px;
    @media screen and (min-width: 768px) {
          font-size: 56px;
          margin-bottom: 51px;
          margin-left: 102px;
        }
                @media screen and (min-width: 1280px) {
                    margin-bottom: 43px;
                                  }
        }


.list {
    list-style: none;
    padding: 0;
}

.sale__product-name {
    font-weight: 600;
    font-size: 22px;
    line-height: 1.18;
    color: var(--accent--color);
    margin: 0;
}

.product__info {
    padding-left: 8px;
    padding-right: 16px;
    padding-bottom: 8px;
    margin-top: 8px;
    margin-bottom: 36px;
}

.sale__item {
    width: 100%;
    display: block;
}

@media screen and (min-width: 768px) {
    .sale__item {
     width: calc((768px - 160px - 38px) / 2);
    }
}


@media screen and (min-width: 1280px) {
    .sale__item {
     width: calc((1280px - 80px - 60px) / 4);
    }
}

.sale__list {
    display: flex;
    gap: 24px;
    flex-wrap: wrap;
    justify-content: center;
}

@media screen and (min-width: 768px) {
    .sale__list {
        gap: 38px;
    }
}

@media screen and (min-width: 1280px) {
    .sale__list {
      gap: 20px;
    }
}

.sale img {
    width: 100%;
    border-radius: 12px;
}


.sale__price {
      margin-top: 8px;
      display: flex;
      gap: 79px;
      align-items: baseline;
    }

.sale__old-price {
    // margin-right: 80px;
    font-weight: 600;
    font-size: 14px;
    line-height: 100%;
    text-decoration-line: line-through;
    color: #8d9289;
}

.sale__new-price {
    font-weight: 600;
    font-size: 20px;
    line-height: 100%;
    color: var(--accent--color);
}

.sale__button {
    width: 153px;
    height: 51px;

    background: #ffffff;
    border-radius: 16px;
    text-align: center;
    padding-top: 16px;
    padding-bottom: 16px;

    display: block;
    margin: 0 auto;

    text-decoration: none;

    font-weight: 700;
    font-size: 16px;
    line-height: 1.18;
    color: #01383a;

    cursor: pointer;
}

.sale__button:hover {
box-shadow: 0px 4px 20px rgba(255, 255, 255, 0.5), 0px 4px 10px rgba(134, 229, 67, 0.1);
}

@media screen and (min-width: 768px) {
    .sale__button {
 width: 195px;   
 height: 65px;
 font-size: 28px;
 line-height: 1.14;
}
}

.link {
    text-decoration: none;
}


@media screen and (min-width: 768px) {
.sale__product-mobile {
display: none;
}
}

@media screen and (max-width:767px) {
    .sale__product-tablet {
        display: none;
    }
}

@media screen and (min-width: 1280px) {
    .sale__product-tablet {
        display: none;
    }
}

@media screen and (max-width: 1279px) {
    .sale__product-desktop {
        display: none;
    }
}


.line__first-up {
    width: 132px;
    border: 1px solid #7BE34A;
    box-shadow: 0px 4px 25px rgba(123, 227, 74, 0.25);
    position: absolute;
    top: 36px;
    left: 0;
    @media screen and (min-width: 768px) {
        width: 325px;
        top: 85px;
    }
 }

 .line__second-up {
    height: 8px;
    border-left: 1px solid #7BE34A;
    box-shadow: 0px 4px 25px rgba(123, 227, 74, 0.25);
    position: absolute;
    top: 29px;
    left: 131px;
    @media screen and (min-width: 768px) {
            height: 20px;
            left: 324px;
            top: 66px;
        }
 }

 .line__third-up {
    width: calc(100% - 131px);
    border: 1px solid #7BE34A;
    box-shadow: 0px 4px 25px rgba(123, 227, 74, 0.25);
    position: absolute;
    top: 28px;
    right: 0;
    @media screen and (min-width: 768px) {
            width: calc(100% - 324px);
            top: 65px;
                    }
 }

 .line__first-bottom {
    width: 135px;
    border: 1px solid #28780D;
    box-shadow: 0px 4px 25px rgba(40, 120, 13, 0.25);
    position: absolute;
    top: 40px;
    left: 0;
    @media screen and (min-width: 768px) {
            width: 335px;
            top: 93px;
        }
 }

 .line__second-bottom {
     height: 9px;
     border: 1px solid #28780D;
     box-shadow: 0px 4px 25px rgba(40, 120, 13, 0.25);
     position: absolute;
     top: 33px;
     left: 134px;
    @media screen and (min-width: 768px) {
            height: 20px;
            left: 334px;
            top: 74px;
        }
 }

 .line__third-bottom {
     width: calc(100% - 134px);
     border: 1px solid #28780D;
     box-shadow: 0px 4px 25px rgba(40, 120, 13, 0.25);
     position: absolute;
     top: 32px;
     right: 0;
    @media screen and (min-width: 768px) {
            width: calc(100% - 334px);
            top: 74px;
        }
 }
 